import tagSexAge from '@/components/common/tag-sex-age.vue'
export default {
	props:{
		item: Object,
		itemClass: {
			type: String,
			default: 'fadeInLeft'
		}
	},
	data () {
		return {}
	},
	components: {
		tagSexAge
	},
	methods: {
		follow (follow) {
			this.$emit('fixFollow', follow)
		},
		imgDetail (index) {
			uni.previewImage({
				urls:this.item.morePic,
				current:index
			})
		}
	}
}
